.wp-block-b-blocks-info-box {
	* {
		box-sizing: border-box;
	}

	.bBlocksInfoBox {
		display: inline-block;
		max-width: 100%;

		.infoBoxIcon {
			display: inline-flex;
			align-items: center;
			justify-content: center;
			padding: 15px;

			i {
				font-style: inherit;
			}
		}

		.infoBoxButton {
			display: inline-block;
			text-decoration: none;
		}
	}
}